  width: 100%;
  height: 100%;
  position: relative;
  /* 地图框样式 */
  #viewDiv{
    width: 100%;
    height: 100%;
  }
  #legendbar {
    &>span {
      background: #08304a;
      border: 1px solid #93c2e6;
      opacity: .8;
      color: #00e5ff;
      font-size: 16px;
      line-height: 30px;
      text-align: center;
      letter-spacing: 20px;
      padding-left: 15px;
      height: 30px;
      display: block;
    }
    .legend_box {
      background: rgba(1,51,102,.85);
      border: 1px solid rgba(147,194,230,.8);
      border-top: none;
      padding: 10px 0;
      color: #fff;
    }
  }
}
